'use client';

import Link from 'next/link';
import { useState, useEffect } from 'react';

export default function Header() {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const baseTextColor = scrolled ? 'text-gray-900' : 'text-white';
  const navLinkClass = `text-sm tracking-wider uppercase transition-colors hover:text-[var(--gold)] ${baseTextColor}`;

  const scrollToTop = () => window.scrollTo({ top: 0, behavior: 'smooth' });

  return (
    <header
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-500 ${
        scrolled
          ? 'bg-white/95 backdrop-blur-md shadow-sm'
          : 'bg-transparent'
      }`}
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-20">
          <Link
            href="/"
            onClick={() => {
              if (typeof window !== 'undefined') {
                scrollToTop();
              }
            }}
            className={`text-2xl font-light tracking-[0.2em] transition-colors hover:text-[var(--gold)] ${baseTextColor}`}
          >
            <span className="font-serif">Araish</span>
          </Link>

          <nav className="hidden md:flex items-center gap-8">
            <Link href="/" className={navLinkClass}>
              Collections
            </Link>
            <Link href="/#about" className={navLinkClass}>
              About
            </Link>
            <Link href="/#contact" className={navLinkClass}>
              Contact
            </Link>
          </nav>
        </div>
      </div>
    </header>
  );
}
